<template>
  <div
    class="chartAll"
    @mouseenter="showArrow = true"
    @mouseleave="showArrow = false"
  >
    <transition name="slide-fade" v-if="chartArry.length > 3">
      <img
        style="
          position: absolute;
          width: 0.25rem;
          top: 50%;
          left: -3px;
          transform: translateY(-50%);
          cursor: pointer;
          z-index: 9;
        "
        src="@/assets/home_view/arrow.png"
        alt=""
        @click="arrowLeft()"
        v-show="showArrow"
      />
    </transition>
    <transition name="slide-fade" v-if="chartArry.length > 3">
      <img
        style="
          position: absolute;
          width: 0.25rem;
          top: 50%;
          right: -4px;
          transform: translateY(-50%) rotate(180deg);
          cursor: pointer;
          z-index: 9;
        "
        src="@/assets/home_view/arrow.png"
        alt=""
        @click="arrowRight()"
        v-show="showArrow"
      />
    </transition>
    <div v-for="(item, index) in chartArry" :key="index">
      <div class="subChart" :id="item.fieldName"></div>
      <div class="chartContent">
        <p>
          <img
            :src="require('@/assets/homeIcon/' + item.name + '.png')"
            alt=""
            style="width: 0.2rem"
          />
        </p>
        <p style="font-size: 0.16rem">{{ item.name }}({{ item.value }})</p>
      </div>
    </div>
  </div>
</template>
<script>
import { comprehensiveHomePage } from "@/network/home_view";
export default {
  data() {
    return {
      chartArry: [],
      showArrow: false,
    };
  },
  mounted() {
    this.comprehensiveHomePageFun();
    this.setChartFun()
  },
  methods: {
    comprehensiveHomePageFun() {
      this.$parent.$parent.changeCHLoad(true);
      let arry = [
        {
          name: "自来水",
          // value: content.coldOverAll,
          value: "50.12%",
          fieldName: "cold",
        },
        {
          // name: content.compressedAirText,
          name:"天然气",
          // value: content.compressedOverAll,
          value: "41.11%",
          fieldName: "compressed",
        },
        {
          name: "电",
          // value: content.elecOverAll,
          value: "10.9%",
          fieldName: "elec",
        },
        {
          // name: content.gasText,
          // value: content.gasOverAll,
          name:"天然气",
          value: "20.12%",
          fieldName: "gas",
        },
        {
          // name: content.hotText,
          // value: content.hotOverAll,
          name:"压缩空气",
          value: "30.42%",
          fieldName: "hot",
        },
        {
          // name: content.n2Text,
          // value: content.n2OverAll,
          name:"氮气",
          value: "50.32%",
          fieldName: "n2",
        },
        {
          // name: content.steamText,
          // value: content.steamOverAll,
          name:"中水",
          value: "60.2%",
          fieldName: "steam",
        },
        {
          // name: content.reclaimedWaterText,
          // value: content.reclaimedWaterOverAll,
          name:"冷凝水",
          value: "90.42%",
          fieldName: "reclaimedWater",
        },
      ];
      this.chartArry = arry.filter((item) => {
        return Number(item.value.substring(0, item.value.length - 1)) > 0;
      });
      setTimeout(() => {
        this.chartArry.forEach((item) => {
          this.setChartFun(item);
        });
      }, 1000);
      this.$parent.$parent.changeCHLoad(false);
    },
    /**
     * 生成图表
     * **/
    setChartFun(item) {
      let value = item.value.substring(0, item.value.length - 1);
      let chart = document.getElementById(item.fieldName);
      let myChart = this.$echarts.init(chart);
      myChart.clear();
      let option = {
        color: ["#00EEFF","#00AEFF"],
        series: [
          {
            name: "",
            type: "pie",
            radius: ["60%", "85%"],
            avoidLabelOverlap: false,
            label: {
              show: false,
              position: "center",
            },
            emphasis: {
              label: {
                show: false,
                fontSize: "40",
                fontWeight: "bold",
              },
            },
            labelLine: {
              show: false,
            },
            data: [
              { value: Number(value), name: item.name },
              { value: 100 - Number(value), name: "总量" },
            ],
          },
        ],
      };
      myChart.setOption(option);
      window.addEventListener("resize", function () {
        myChart.resize();
      });
    },
    /**
     * **/
    arrowLeft() {
      let shift = this.chartArry.shift(); //删除数组的第一项
      this.chartArry.push(shift);
    },
    arrowRight() {
      let pop = this.chartArry.pop(); //删除数组的第一项
      this.chartArry.unshift(pop);
    },
  },
};
</script>
<style scoped lang="less">
.chartAll {
  width: 100%;
  height: 100%;
  flex-grow: 1;
  display: flex;
  overflow: hidden;
  .subChart {
    width: 100%;
    height: 100%;
    position: absolute;
  }
  > div {
    width: 33.33%;
    height: 100%;
    position: relative;
    flex-shrink: 0;
  }
}
.chartContent {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 99999999;
}
</style>